"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { getSupabaseBrowserClient } from "@/lib/supabase/client"

export default function StatisticsManager() {
  const [stats, setStats] = useState<any[]>([])

  useEffect(() => {
    async function fetchData() {
      try {
        const supabase = getSupabaseBrowserClient()
        const { data, error } = await supabase
          .from("site_content")
          .select("content")
          .eq("type", "statistics")
          .order("updated_at", { ascending: false })
          .limit(1)
          .maybeSingle()

        if (error) {
          if (error.code && error.message) {
            console.error("Error fetching statistics:", error.message, error.code)
          }
          return
        }

        if (data?.content) {
          const content = Array.isArray(data.content) ? data.content : []
          setStats(content)
        }
      } catch (err) {
        console.error("Unexpected error fetching statistics:", err)
      }
    }
    fetchData()
  }, [])

  function handleChange(idx: number, field: string, value: string) {
    const newStats = [...stats]
    newStats[idx][field] = value
    setStats(newStats)
  }

  async function handleSave() {
    try {
      const supabase = getSupabaseBrowserClient()
      
      // Delete all existing records with this type to avoid duplicates
      await supabase
        .from("site_content")
        .delete()
        .eq("type", "statistics")

      // Insert new record
      const { error } = await supabase
        .from("site_content")
        .insert({ type: "statistics", content: stats })

      if (!error) {
        alert("Statistics updated!")
        // Refresh data
        const { data } = await supabase
          .from("site_content")
          .select("content")
          .eq("type", "statistics")
          .order("updated_at", { ascending: false })
          .limit(1)
          .maybeSingle()
        if (data?.content) {
          const content = Array.isArray(data.content) ? data.content : []
          setStats(content)
        }
      } else {
        alert(`Error updating statistics: ${error.message}`)
        console.error(error)
      }
    } catch (err) {
      alert("Unexpected error updating statistics")
      console.error(err)
    }
  }

  return (
    <div className="space-y-6 max-w-2xl mx-auto p-4">
      <h2 className="text-2xl font-bold">Statistics Settings</h2>

      {stats.map((item, idx) => (
        <div key={idx} className="border p-4 rounded space-y-2">
          <Input
            placeholder="Icon"
            value={item.icon}
            onChange={e => handleChange(idx, "icon", e.target.value)}
          />
          <Input
            placeholder="Title"
            value={item.title}
            onChange={e => handleChange(idx, "title", e.target.value)}
          />
          <Input
            placeholder="Description"
            value={item.description}
            onChange={e => handleChange(idx, "description", e.target.value)}
          />
        </div>
      ))}

      <Button onClick={handleSave}>Save</Button>
    </div>
  )
}
